//   .  : - mobile:  ,    414px;
// - tablet: 1024px;
// - desktop: 1280px


//  :     ,      .  ,       ,        @include ().            .


@mixin mobile {
    @media screen and (min-width:414px) {
        @content;
    }
}

@mixin tablet {
    @media screen and (min-width:1024px) {
        @content;
    }
}

@mixin pc {
    @media screen and (min-width:1280px) {
        @content;
    }
}

