.section {
    background-color: inherit;
    
    @media (min-width: 1024px) {
        padding: 0px 32px;
    }

    @media (min-width: 1280px) {
        padding: 0px 60px;
    }

    &__title {
            font-family: 'Times New Roman';
            font-weight: 400;
            font-size: 32px;
            line-height: 1.16;
            text-transform: uppercase;
            margin-bottom: 16px;
        }

    &__text {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 1.25;
        letter-spacing: 0.02em;
        color: $color-text-primary;
        margin-bottom: 32px;
    }
}